  select {
    /* Reset Select */
    appearance: none;
    outline: 10px red;
    border: 0;
    box-shadow: none;
    /* Personalize */
    flex: 1;
    padding: 0 1em;
    color: #fff;
    background-color: #4B4B4BB2;
    background-image: none;
    cursor: pointer;
  }
  /* Remove IE arrow */
  /* select::-ms-expand {
    display: none;
  } */
  /* Custom Select wrapper */
  .select {
    position: relative;
    display: flex;
    width: 20px;
    height: 35px;
    border-radius: .25em;
    overflow: hidden;
  }
  /* Arrow */
  .select::after {
    content: '\25BC';
    position: absolute;
    top: 0;
    right: 0;
    padding: 1em;
    background-color: #34495e;
    transition: .25s all ease;
    pointer-events: none;
  }
  /* Transition */
  .select:hover::after {
    color: #f39c12;
  }

