.outerCont{
    width: 35%;
    flex-direction: column;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #1A1E28;
    border-radius: 20px;
    border-bottom: 5px solid #FFF278;

}
.img{
    width: 80%;
    margin-top: 1rem;
}
.img img{
    width: 73%;
    height: 82%;
    margin-left:3rem
}
.content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.button{
    background: var(--gold, linear-gradient(131deg, #FFF278 31.55%, #F8B533 100%));
    border-radius: 21px;
    text-align: center;
    width: 15rem;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    margin-top: 3rem;
    margin-bottom: 64px;

}
.heading{
    color: var(--white, #FFF);
    text-align: center;
    font-family: Poppins;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}
.text{
    color: var(--white, #FFF);
    text-align: center;
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    width: 63%;
    font-weight: 400;
    line-height: normal;
}
.wrapperAr{
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(135deg, rgba(76, 201, 255, 0.10) 0%, rgba(83, 125, 236, 0.40) 48.96%, rgba(76, 201, 255, 0.10) 100%);
    border-radius: 19px;
    width: 2rem;
    border: 2px solid gold;
    height: 2rem;
}
.wrapperAr img{
    height: 66%;
    width: 65%;
}

.topWrapper {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    padding: 10px 10px;
}

@media screen and (max-width: 500px) {
    .outerCont {
        width: 100%;
        height: calc(100% - 40px);
    }
}